.toggle {
  margin-right: 10px;
  -webkit-appearance: none;
          appearance: none;
  vertical-align: middle;
  width: 30px;
  height: 15px;
  display: inline-block;
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #200E2E;
  background-color: var(--secondary-color);
  transition: background-color ease 0.3s;
}

.toggle:before {
  content: "";
  display: block;
  position: relative;
  z-index: 2;
  width: 10px;
  height: 10px;
  background: #FF9700;
  background: var(--primary-color);
  left: 2px;
  top: 2.5px;
  border-radius: 50%;
  text-transform: uppercase;
  font-weight: bold;
  color: #FF9700;
  color: var(--primary-color);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transition: all cubic-bezier(0.3, 1.5, 0.7, 1) 0.3s;
}

.toggle:checked:before {
  left: 16px;
}

.progress-bar-wrapper {
  display: none;
}


/* --------- styles for multi dropdowns --------------*/
.multi-dropdown{
  padding: 4px 9px;
}

.multi-dropdown__icon{
  color: #FF9700;
  color: var(--primary-color);
  background-color: #200E2E;
  background-color: var(--secondary-color);
  padding: 4px 9px;
  border-radius: 5px;
  cursor: pointer;
  transition: 500ms;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
  font-size: 20px;
}

.multi-dropdown__icon:hover{
  opacity: 0.90;
}

.multi-dropdown__dropdown{
  background-color: #200E2E;
  background-color: var(--secondary-color);
  color: #FF9700;
  color: var(--primary-color);
  border: solid 10px #200E2E;
  border: solid 10px var(--secondary-color);
  width: 18em;
  border-radius: 6px;
  position: absolute;
  margin-left: -15.8em;
  z-index: 10;
  list-style-type: none;
  margin-top: 5px;
}

.multi-dropdown__dropdown.--closed{
  display: none;
}

.multi-dropdown__dropdown > div{
  background-color: #FF9700;
  background-color: var(--primary-color);
  color:  #200E2E;
  color:  var(--secondary-color);
  margin-bottom: 7px;
  transition: 300ms;
}
.multi-dropdown__dropdown > div:last-of-type{
  margin-bottom: 0;
}

.multi-dropdown__dropdown div:hover{
  opacity: 0.95;
}

.single-dropdown{
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
}

.single-dropdown.--hidden{
  display: none;
}

.single-dropdown p {
  font-weight: 500;
  text-transform: capitalize;	
  display: flex;
  grid-gap: 10%;
  gap: 10%;
  padding-left: 10px;
  padding-top: 3px;
  cursor: pointer;
  margin-bottom: 0;
  align-items: center;
}
.single-dropdown > div {
  margin-top: 15px;
  text-align: left;
  padding: 0 5px;
  display: flex;
  flex-direction: column;
  grid-gap: 3px;
  gap: 3px;
}

.single-dropdown > div.hide{
  display: none;
}

.dropdown-pill{
  background-color:  #200E2E;
  background-color:  var(--secondary-color);
  color:  #FF9700;
  color:  var(--primary-color);
  border-radius: 3px;
  padding: 5px 10px;
  cursor: pointer;
  line-height: 16px;
}

.dropdown-pill a, .dropdown-pill span{
  text-decoration: none;
  color:  #200E2E;
  color:  var(--secondary-color);
}

.dropdown-pill div{
  display: flex;
  align-items: center;
}

.dropdown-pill.--left{
  text-align: left;
}


.single-dropdown a{
  text-decoration: none;
  color:  #FF9700;
  color:  var(--primary-color);;
}

.toggle.--small {
  width: 35px;
  height: 20px;
}

.toggle:before {
  width: 15px;
  height: 15px;
}

.filled-icon{
  background-color: #200E2E;
  background-color: var(--secondary-color);
  color: #FF9700;
  color: var(--primary-color);
